* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(237, 177, 241, .4);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

img {
  object-fit: fill;
  width: 100%;
  height: 200px;
  display: block;
}

.header {
  width: 100%;
  z-index: 10;
  opacity: 1;
  background-image: linear-gradient(90deg, #fc739c 0%, #8b66f1 100%);
  position: fixed;
}

.on-scroll {
  opacity: .9;
}

.wrapper {
