}

.search-form {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-field {
  position: relative;
}

input {
  height: 40px;
  width: 300px;
  color: #2a2438;
  background-color: #edb1f1;
  border: 2px solid #edb1f1;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
  font-family: Tilt Neon, cursive;
  transition: border-color .3s ease-in-out;
}

.btn-search {
  height: 40px;
